@import url(http://fonts.googleapis.com/css?family=EB+Garamond);

html, body {
  height: 100%;
  font-family:Garamond, Georgia, serif;
}

li {
  list-style-type: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.img-thumbnail {
  max-height: 18rem;
}

.about-text,
.randomItemTitle {
  text-align: left;
}

.randomImage {
    max-width: 60%;
    margin: 0px auto;
}
.randomImage img {
  max-width: 100%;
}

resume-container.container {
  display: block;
  height: 100vh;
}
.resume-frame {
  height: 100vh;
  width: 75vh;
}

.nav-Container {
  margin-top: 1em;
  margin-bottom: 1em;
}

.footer {
  padding-top: 1em;
  padding-bottom: 3em;
}

.footer .btn {
  padding: 0px 10px;
}